
.headerBg {
    background: #fff;
    padding: 2rem;
    margin: 5rem;
}

.game-title {
    color: #263238;
    text-align: center;
}

.scoreboard {
    position: relative;
    border: 3px solid white;
    padding: 15px;
    width: 200px;
    margin: 20px auto;
    color: white;
    font-size: 1.5rem;
    border-radius: 5px;
    text-align: center;
}
.user-badge {
    background: darkmagenta;
    border-radius: 1rem;
    width: 80px;
    font-size: 1rem;
}
#user-label {
    position: absolute;
    top: 40%;
    left: -30%;
}

#computer-label {
    position: absolute;
    top: 40%;
    right: -30%;
}
#user-score, #computer-score {
    font-size: 3rem;
    font-weight: 600;
}
#played {
    font-size: 4rem;
    color: goldenrod;
}

.result h2 {
    color: white;
    text-align: center;
    font-weight: 800;
}
.result p {
    font-size: 2rem;
    color: white;
    text-align: center;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.choices {
    margin-bottom: 5rem;
    padding: 0;
    text-align: center;
}

.choice {
    border: 4px solid white;
    display: inline-block;
    width: 100px;
    margin: 0 1rem;
    padding: 1rem;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.35s ease;
}

.choice:hover {
    background: #000a12;
}

.action-message {
    text-align: center;
    color: white;
    font-size: 1.5rem;
    font-weight: 600;
    letter-spacing: 1px;
}

.greenglow {
    background: #4dcc7d;
    transform: scale(1.2);
    transition: all 0.3s ease-in-out;
}

.redglow {
    background: #fc121b;
    transform: scale(1.2);
    transition: all 0.3s ease-in-out;
}

.greyglow {
    background: #787878;
    transform: scale(1.2);
    transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 600px) {
    .headerBg {
        border-radius: 1rem;
        padding: 0.5rem;
        margin-bottom: 0;
    }
    .game-title{
        font-size: 2rem;
    }
    .result h2 {
        font-size: 1rem;
    }
    #played {
        font-size: 2rem;
    }
    .result p {
        font-size: 1.2rem;
    }
    .choice {
        width: 80px;
        margin: 0.3rem 1rem;
    }
}